@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Euphoria+Script&display=swap);
* {
  box-sizing: border-box;
}

body, html {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  font-family: 'Roboto';
  font-weight: 400;
  background-color: #0c1832;
  overflow-x: hidden;
}

ul, li {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

#root {
  height: 100%;
  width: 100%;
  position: relative;
}

.wrapper, .app {
  height: 100%;
  width: 100%;
}

.wrapper {
  position: relative;
  z-index: 5;
}

.container {
  width: 1100px;
  max-width: 100%;
  margin: 0 auto;
  padding: 0 2%;
}

.container--position {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (max-width:768px) {
  .container--position {
    position: relative;
    z-index: 2;
    height: 100vh;
    display: flex;
    flex-direction: column;
  }
}
.logo--loading .logo__link{flex-direction:column;align-items:center}.logo--loading .logo__link-icon{margin-bottom:10px;height:200px}.logo--loading .logo__link-text{font-size:50px;line-height:46px}.logo__link{display:inline-flex;align-items:center}.logo__link-text{color:#095d60;font-weight:700;font-size:20px;font-family:"Kaushan Script", cursive}.logo__link-icon{height:80px}@-webkit-keyframes pulse{from{transform:scale(1)}50%{transform:scale(1.1)}to{transform:scale(1)}}@keyframes pulse{from{transform:scale(1)}50%{transform:scale(1.1)}to{transform:scale(1)}}@media screen and (max-width: 480px){.logo{display:none}}

.menu__list{display:flex;align-items:center}.menu__item{margin-right:20px}.menu__item:last-child{margin-right:0}.menu__link{font-size:17px;font-weight:700;border-bottom:1px solid transparent;transition:.4s}.menu__link:hover{border-bottom-color:black}@media screen and (max-width: 768px){.menu{display:none}}

