r-team__item{margin-bottom:125px}.our-team__item--even .our-team__avatar-line--top{width:83px}.our-team__avatar-line--top{width:83px}}

.what-new__subtitle{color:#00b69d;font-size:26px;margin-left:74px;transform:skewY(-11.5deg);font-family:"Kaushan Script", cursive}.what-new__text{color:#fff;font-weight:500;padding:0 15%;margin-top:100px;text-align:center;line-height:30px}@media screen and (max-width: 480px){.what-new__text{padding:0 6%;line-height:24px;margin-top:66px}.what-new .title{font-size:51px}.what-new__subtitle{margin-bottom:25px}}

