.news{display:flex;margin-top:160px}.news__col{width:50%}.news__col--other{background-color:#fff}.news-list{padding-right:10%}.news-list__head{display:flex;align-items:center;margin-bottom:15px}.news-list__head-title{font-size:16px;font-feature-settings:"c2sc";font-variant:small-caps;letter-spacing:0.55em;text-transform:lowercase;color:rgba(255,255,255,0.8);margin-left:7px}.news-list__head-icon{height:20px;width:20px}.news-list__item{padding-bottom:60px;margin-bottom:60px;border-bottom:1px solid rgba(255,255,255,0.15)}.news-list__item:last-child{padding-bottom:0;margin-bottom:0;border-bottom:none}.news-list__info{font-weight:400;font-style:italic;font-size:18px;line-height:1.8;color:#ffa88c;margin-bottom:20px}.news-list__created-at{color:#fff}.news-letter{padding:60px}.news-letter__title{font-weight:700;font-size:35px;color:#0b1e38;margin-bottom:30px}.news-letter__info{font-size:20px;color:rgba(7,20,37,0.8);margin-bottom:20px;line-height:29px}@media screen and (max-width: 768px){.news{margin-top:260px;flex-direction:column}.news__col{width:100%}.news__col--other{margin-top:61px}.news-list{padding-right:0}.news-list__item{text-align:center}.news-list__head{justify-content:center}}@media screen and (max-width: 480px){.news-letter__title{font-size:25px;margin-bottom:18px}.news-letter__info{font-size:16px}}

