.main-content{height:100%;width:100%;margin-top:-24%}@media screen and (max-width: 768px){.main-content{margin-top:0}}

.load-section{height:100%;width:100%;position:fixed;z-index:25;top:0;left:0;display:flex}.load-section--animate{pointer-events:none}.load-section--animate .load-section__col:first-child{left:-50%}.load-section--animate .load-section__col--right{left:auto;right:-50%}.load-section__col{height:100%;width:50%;position:absolute;top:0;left:0;transition:0.4s;background:#7970a9}.load-section__col--right{right:0;left:auto}.load-section__center{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);perspective:200px}.load-section__logo{-webkit-animation:test 5s 0.5s ease infinite;animation:test 5s 0.5s ease infinite;transform-style:preserve-3d;transition:0.5s}.load-section__logo .logo{display:inline-flex}.load-section__logo .logo__link-text{color:#fff}.load-section__indicator{text-align:center;margin-top:50px;color:#fff;font-size:20px}@-webkit-keyframes test{from{transform:rotateY(0deg)}to{transform:rotateY(360deg)}}@keyframes test{from{transform:rotateY(0deg)}to{transform:rotateY(360deg)}}

.full-menu{position:fixed;top:0;left:0;right:0;bottom:0;height:100%;width:100%;z-index:11;opacity:1;visibility:hidden;transition:opacity 0.35s, visibility 0.35s, width 0.35s;display:flex;align-items:center;justify-content:center}.full-menu:before{content:"";background:#282782;left:-55%;top:0;width:50%;height:100%;position:absolute;transition:left 0.35s ease}.full-menu:after{content:"";background:#282782;right:-55%;top:0;width:50%;height:100%;position:absolute;transition:all 0.35s ease}.full-menu.open{opacity:0.92;visibility:visible;height:100%}.full-menu.open:before{left:0}.full-menu.open:after{right:0}.full-menu.open .menu{z-index:5;display:flex}.full-menu.open .menu__item{-webkit-animation:fadeInRight 0.5s ease forwards;animation:fadeInRight 0.5s ease forwards;-webkit-animation-delay:0.35s;animation-delay:0.35s}.full-menu.open .menu__item:nth-of-type(2){-webkit-animation-delay:0.45s;animation-delay:0.45s}.full-menu.open .menu__item:nth-of-type(3){-webkit-animation-delay:0.55s;animation-delay:0.55s}.full-menu.open .menu__item:nth-of-type(4){-webkit-animation-delay:0.65s;animation-delay:0.65s}.full-menu .menu__list{flex-direction:column;align-items:center}.full-menu .menu__item{margin-bottom:20px;margin-right:0}.full-menu .menu__item:last-child{margin-bottom:0}.full-menu .menu__link{color:#fff;font-size:28px}

.ham{cursor:pointer;-webkit-tap-highlight-color:transparent;transition:transform 400ms;position:fixed;right:0;top:20px;z-index:20;display:none}.hamRotate.active{transform:rotate(45deg)}.hamRotate180.active{transform:rotate(180deg)}.line{fill:none;transition:stroke-dasharray 400ms, stroke-dashoffset 400ms;stroke:#be7070;stroke-width:5.5;stroke-linecap:round}.ham7 .top{stroke-dasharray:40 82}.ham7 .middle{stroke-dasharray:40 111}.ham7 .bottom{stroke-dasharray:40 161}.ham7.active .top{stroke-dasharray:17 82;stroke-dashoffset:-62px}.ham7.active .middle{stroke-dashoffset:23px}.ham7.active .bottom{stroke-dashoffset:-83px}@media screen and (max-width: 768px){.ham{display:inline-flex}}


